import { Component,OnInit } from '@angular/core';
import {  UpdateService } from '../../services/update.service';
import {  ReportService } from '../../services/report.service';
import { DataService } from '../../services/data.service';
import { invoices } from '../../invoice/product_model';
import { Router } from '@angular/router';
import {FormBuilder, FormGroup,FormControl, Validators} from '@angular/forms';
@Component({
    selector: 'revenue',
    templateUrl: './revenue.html',
    styleUrls: ['./revenue.css']
})
export class RevenueReport {
    get_invo = new invoices(1,0,'','',0,0,0,0,0,0,0);
    data : any [] =[];
    items = [];
    itemCount = 0;
    to_date : string;
    from_date : string;
    view : boolean = false;
    length : number = 0;
    total_revenue : number =0;
     total_tax :number = 0;
     today: number = Date.now();
     total_due : number = 0;

       dateForm = this.fb.group({                  
                                                  
                                                  from_date : '1987-09-10',
                                                  to_date: '2055-08-09',
                                                  option : 2
                                                 
                                              
                                         });

    constructor( private fb: FormBuilder, private ds : DataService, private uS: UpdateService, private rs: Router ,private Rs: ReportService ) {
       
    }
ngOnInit() {
   
   this.get_report();
    
   
  }

 get_report()
  {

    this.from_date = this.Rs.from_date ;
    this.to_date = this.Rs.to_date;



    this.dateForm.patchValue({from_date:  this.from_date, to_date: this.to_date })

    
    this.ds.get_report_revenue(this.dateForm.value)
       .subscribe((jsonData) => { this.getjson(jsonData)
                      },(err) => console.error(err),
                      
                      );

  }

  getjson(s:any)
  {

    this.view = true;
     this.data = s;
     this.length = this.data.length;
   
    this.express(this.length);

  }

  express(s: number)
  {
       this.total_revenue = 0;
       this.total_due = 0 ;
        this.total_tax = 0 ;
       for(var i =0; i < s ; i++)
       {
         this. total_revenue = this.total_revenue + this.data[i].TOTAL_AMOUNT;
         this.total_tax = this.total_tax + this.data[i].TAX_COLLECTED;
         this.total_due = this.total_due + this.data[i].TOTAL_DUE
       }

  }

    public options:any = {
    orderMulti: false,
    className: ['table-striped'],
    language: "en"
};
public paging: any = {
    itemsPerPage: 10,
    itemsPerPageOptions: [10, 25, 50, 100],
    maxSize: 5
}
public columns:Array<any> = [

  { 
    title: 'INVOICE NUMBER', 
    name: 'INVOICE_NUMBER', 
    sort: true, 
    defaultSortOrder: 'asc',  
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by invoice number'
        }
    } 
},
{ 
    title: 'DATE', 
    name: 'createdAt', 
    sort: true, 
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by Date'
        }
    } 
},
{ 
    title: 'TRAN_TYPE', 
    name: 'TRAN_TYPE', 
    sort: true, 
    defaultSortOrder: 'asc',  
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by name'
        }
    } 
},
{ 
    title: 'TAX_COLLECTED', 
    name: 'TAX_COLLECTED', 
    sort: true, 
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by Vat ID'
        }
    } 
},
{ 
    title: 'TOTAL_AMOUNT', 
    name: 'TOTAL_AMOUNT', 
    sort: true, 
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by gross total'
        }
    } 
},
{ 
    title: 'PARTIAL PAYMENT', 
    name: 'IS_PARTIAL_PAY', 
    sort: true, 
    filter: {
        type: 'default', 
        controlType: 'default',
        config: {
            placeholder: 'Filter by payment'
        }
    } 
},
 

];

}