import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { DataService } from '../../../services/data.service';
import { UpdateService } from '../../../services/update.service';
@Component({
  selector: 'deleteinvo',
  templateUrl: './delete_invoice.html',
  styleUrls: ['./delete_invoice.css']
})
export class DeleteinvoComponent implements OnInit {


 dateForm : FormGroup;
 
 invoice_number : number;
   
 constructor(private fb: FormBuilder, private ds :DataService, private router : Router, private us : UpdateService) { }


  ngOnInit() {
 this.dateForm = this.fb.group({                  
                                                  invo_number : 0,
                                                 
                                         });




                                      
    
  }
  
yes()
{
 this.invoice_number = this.us.invo_number;
 console.log('in',this.invoice_number);
this.dateForm.patchValue ({invo_number : this.us.get_invoice_number()});

this.us.delete_invo(this.dateForm.value)
      .subscribe((jsonData) => { this.getjson(jsonData) },(err) => console.error(err),);
}
no()
{
  this.router.navigate(['/dash']);
}


getjson(json :any)
  {
    
  
   if (json.msg ==='Invoice Deleted')
   {
      alert('Invoice deleted sucessfully')
      this.router.navigate(['/dash']);
   }

}
  





}