import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators, FormBuilder }  from '@angular/forms';
import { DataService } from '../../services/data.service';
import { UpdateService } from '../../services/update.service';
@Component({
  selector: 'stock-add',
  templateUrl: './add.html',
  styleUrls: ['./add.css']
})
export class Stock_addComponent implements OnInit {
 
   s : any = {"k": "P"};
     arrayOfKeyValues: any[] = [];
   isclicked : boolean= false;
    avil_qty : any;
 
 addstockForm: FormGroup;
 frut : FormGroup ;

  constructor(private fb: FormBuilder, private router: Router , private ds: DataService , private us : UpdateService) {   }
  ngOnInit()  {
      this.get_products();
         this.addstockForm = this.fb.group({ 

      supplyer_name : '',
      quantity : '',
      remarks : '', 
      product_name : '',
      oldqty: ''
      
    });
           this.frut = this.fb.group({ 

    
      remarks : '', 
      product_name : ''
      
    });

  }
  

cus_blur (p :any)
{
  this.addstockForm.patchValue({product_name : p});
  
  this.frut.patchValue({product_name: p});
  this.us.get_avail_qty(this.frut.value)
   .subscribe((jsonData) => { this.getjson3(jsonData)
                      },(err) => console.error(err),
                       
                      );


}
add_stock_product()
{
  
  this.addstockForm.patchValue({oldqty: this.avil_qty})
  this.us.update_stock(this.addstockForm.value)
 .subscribe((jsonData) => { this.getjson2(jsonData)
                      },(err) => console.error(err),
                      
                      );
}
  

    getjson3(json :any)
  {
    

 this.avil_qty = json.msg;
  
   
  }

      getjson2(json :any)
  {
    


 if(json.msg === 'Successfully updated')
 { 
   
   alert('Stock Updated Sucessfully');
   this.router.navigate(['/dash']);

 }
  else 
  {
       alert('Something Wrong');
  }
   
  }



   get_products()
{
 this.ds.get_product_list(this.s)
  .subscribe((jsonData) => { this.getjson(jsonData)
                      },(err) => console.error(err),
                      
                      );
}


      getjson(json :any)
  {
    
  this.arrayOfKeyValues = json;
 
  
   
  }
 
   
  }

