import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { DataService } from '../../../services/data.service';
@Component({
  selector: 'app-add-exp-entry',
  templateUrl: './add-exp-entry.component.html',
  styleUrls: ['./add-exp-entry.component.css']
})
export class AddExpEntryComponent implements OnInit {

 to_date : string;
  from_date : string;
 dateForm : FormGroup;
 
  purchases : any [] = [];
   
 constructor(private fb: FormBuilder, private ds :DataService, private router : Router) { }


  ngOnInit() {
 this.dateForm = this.fb.group({                  
                                                  is_purchase : '',
                                                  from_date : '',
                                                  to_date: '',
                                                  beneficiary : '',
                                                  vocher_no: '',
                                                  amount : '',
                                                  remarks : ''
                                         });
    this.get_exp_type_list();
  }


get_exp_type_list()
{
   this.ds.get_exp_type_list()
      .subscribe((jsonData) => { this.getjson(jsonData)
                      },(err) => console.error(err)
                     
                      );

}

getjson(json :any)
  {
    
   
   for( var i = 0 ; i < json.length; i++)
  this.purchases[i] = json[i].CATEGORY_NAME;
  }
cat_to_date(s:any)
{
  
  this.to_date = s._validSelected.toLocaleDateString();
 
 }

cat_from_date(s:any)
{
 this.from_date =  s._validSelected.toLocaleDateString();
  
}

add_exp()
{
this.dateForm.patchValue({from_date : this.from_date, to_date : this.to_date });

this.ds.add_expence_entry(this.dateForm.value)
.subscribe((Data) => { this.set(Data)
                      },(err) => console.error(err),
                       
                      );
}

set(data :any)
{
 if (data.msg ==='Successfully saved')
   {
      alert('Expence added sucessfully')
      this.router.navigate(['/dash']);
   }

}



}