import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';

import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { DataService } from '../../../services/data.service';
@Component({
  selector: 'app-add-exp-cat',
  templateUrl: './add-exp-cat.component.html',
  styleUrls: ['./add-exp-cat.component.css']
})
export class AddExpCatComponent implements OnInit {


  memberForm: FormGroup;
  purchases = ['Yes','No'];
  constructor(private fb: FormBuilder, private ds :DataService, private router : Router) { }

  ngOnInit() {

     this.memberForm = this.fb.group({ 

      Category_name : ['', [<any>Validators.required, <any>Validators.minLength(3)]],
      is_purchase : ''
     
    });
  }

add_cat()
{
  console.log(this.memberForm.value);
  this.ds.addcategory(this.memberForm.value)
      .subscribe((jsonData) => { this.getjson(jsonData)
                      },(err) => console.error(err)
                      
                      );


}
 getjson(json :any)
  {
    
  
   if (json.msg ==='Successfully saved')
   {
      alert('Category added sucessfully')
      this.router.navigate(['/dash']);
   }

   else if (json.msg === 'Category name already existed')
   {
      alert('Category already existed')
      this.router.navigate(['/dash']);
   }
  }


}