import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup,FormControl, Validators} from '@angular/forms';
import { ReportService }  from '../../services/report.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainReportComponent implements OnInit {
  to_date : string;
  from_date : string;
option : number = 0;

  dateForm = this.fb.group({                  
                                                  
                                                  from_date : '',
                                                  to_date: '',
                                                 
                                              
                                         });
  constructor(private fb: FormBuilder, private rs : ReportService , private router : Router ) { }
 
  ngOnInit() {
  }


cat_to_date(s:any)
{
  
  this.to_date = s._validSelected;
  this.rs.to_date = this.to_date ;
 
 }

cat_from_date(s:any)
{
 this.from_date =  s._validSelected;
  this.rs.from_date = this.from_date ;
}


go_report(s:number)
{



  
  if(s === 1)
  {
    this.option = 1;
  }
  if(s === 2)
  {
 
   
   this.option = 1;
    this.router.navigate(['/report_revenue']);
   
  }
  if(s === 3)
  {
    this.option = 3;
     this.router.navigate(['/report_revenue']);
  }
  else
  {
   this.router.navigate(['/report_revenue']);
  }
}
}