import { Component,OnInit } from '@angular/core';

import {  UpdateService } from '../../services/update.service';
import {  ReportService } from '../../services/report.service';
import { DataService } from '../../services/data.service';

import { Router } from '@angular/router';
import {FormBuilder, FormGroup,FormControl, Validators} from '@angular/forms';
import { items } from './model';

@Component({
    selector: 'tax',
    templateUrl: './tax.html',
    styleUrls: ['./tax.css']
})
export class VatReport {
  
   
    data : any [] =[];
    post_invo = new items(1,[]);
  total_tax : number = 0;
   
    items = [];
    itemCount = 0;
    to_date : string;
    from_date : string;
    view : boolean = false;
    length : number = 0;
   
  
     today: number = Date.now();
     total_due : number = 0;
   
     si : number = 0;
      j :number = 0;
   
       dateFormm = this.fb.group({                  
                                                  
                                                  from_date : '1987-09-10',
                                                  to_date: '2055-08-09',
                                                  option : 2
                                                 
                                              
                                         });

    constructor( private fb: FormBuilder, private ds : DataService, private uS: UpdateService, private rs: Router ,private Rs: ReportService ) {
       
    }
ngOnInit() {
   
   this.get_reports();
    
   
  }

 auto_push()
  {
        this.post_invo.item.push({ "SI":0, "INVOICE_NUMBER":1, "CUSTOMER_NAME":'xxx',"TAX_COLLECTED":0 })

               }

 get_reports()
  {

    this.from_date = this.Rs.from_date ;
    this.to_date = this.Rs.to_date;



    this.dateFormm.patchValue({from_date:  this.from_date, to_date: this.to_date })

    
    this.ds.get_tax_report(this.dateFormm.value)
       .subscribe((jsonData) => { this.getjson(jsonData)
                      },(err) => console.error(err),
                      
                      );

  }

  getjson(s:any)
  {

     this.view = true;
     this.data = s;
     
     this.length = this.data.length;
  
    
   this.express(this.length)

  }

  express(s: number)
  {
      
       for(var i = 0; i < s ; i++)
       {

              {
                
                
                this.auto_push();
                this.post_invo.item[i].SI = i+1;
                this.post_invo.item[i].INVOICE_NUMBER = this.data[i].INVOICE_NUMBER;
                this.post_invo.item[i].CUSTOMER_NAME = this.data[i].CUSTOMER_NAME;
                this.post_invo.item[i].TAX_COLLECTED = this.data[i].TAX_COLLECTED; 
                this.total_tax = +this.total_tax +  +this.data[i].TAX_COLLECTED; 
              }

       }

  }

  
}